@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Moderat";
  src: url("./assets/fonts/Moderat-Regular.ttf");
}
@font-face {
  font-family: "ModeratLight";
  src: url("./assets/fonts/Moderat-Light.ttf");
}
@font-face {
  font-family: "ModeratDark";
  src: url("./assets/fonts/Moderat-Bold.ttf");
}

body {
  font-family: "Moderat";
  overflow-x: hidden;
}
.font-moderat {
  font-family: "Moderat";
}
.font-mod-light {
  font-family: "ModeratLight";
}
.font-mod-dark {
  font-family: "ModeratDark";
}

.bttn {
  @apply w-full sm:w-fit flex items-center justify-center md:px-8 px-4 py-3 border border-transparent;
}
.bttn-2 {
  @apply w-full flex items-center justify-center md:px-8 px-4 py-3 border border-transparent;
}

.bttn-primary {
  @apply text-white bg-[#F00530] hover:bg-red-700 rounded-[4px] focus:ring-2 focus:outline-none focus:ring-red-300 focus:ring-opacity-50 transition-all duration-300 ease-in-out;
}
.bttn-secondary {
  @apply text-white bg-transparent border border-white rounded-[4px] focus:ring-2 focus:outline-none focus:ring-red-300 focus:ring-opacity-50 hover:bg-white hover:text-black transition-all duration-300 ease-in-out;
}
.bttn-tertiary {
  @apply text-[#F00530] bg-transparent border border-[#F00530] rounded-[4px] focus:ring-2 focus:outline-none focus:ring-red-300 focus:ring-opacity-50;
}
.bttn-black {
  @apply text-[#333333] bg-transparent border border-[#333333] rounded-[4px] focus:ring-2 focus:outline-none focus:ring-opacity-50;
}
.bttn-extra {
  @apply text-[#118DFF] bg-transparent focus:ring-2 rounded-[4px] focus:outline-none focus:ring-red-300 focus:ring-opacity-50;
}
.text-incremental {
  @apply text-4xl lg:text-5xl xl:text-7xl;
}
.trans {
  @apply transition-all duration-300 ease-in-out;
}
.shadow-me {
  box-shadow: 0 1px 1px hsl(0deg 0% 0% / 8%), 0 2px 2px hsl(0deg 0% 0% / 8%),
    0 4px 4px hsl(0deg 0% 0% / 8%), 0 8px 8px hsl(0deg 0% 0% / 8%),
    0 16px 16px hsl(0deg 0% 0% / 8%);
}

.swiper-button-next,
.swiper-button-prev {
  color: #f00530 !important;
}

@media (min-width: 640px) {
  .swiper-button-next:after {
    margin-right: 22px;
    margin-bottom: 214px;
  }
  .swiper-button-prev:after {
    margin-left: 22px;
    margin-bottom: 214px;
  }
}
@media (max-width: 640px) {
  .swiper-button-next:after {
    margin-right: 4px;
    margin-bottom: 214px;
  }
  .swiper-button-prev:after {
    margin-left: 4px;
    margin-bottom: 214px;
  }
}

@media (min-width: 768px) {
  .swiper-button-next:after {
    margin-right: 20px;
    margin-bottom: 214px;
  }
  .swiper-button-prev:after {
    margin-left: 17px;
    margin-bottom: 214px;
  }
}

@media (min-width: 1024px) {
  .swiper-button-next:after {
    margin-right: 250px;
    margin-bottom: 214px;
  }
  .swiper-button-prev:after {
    margin-left: 244px;
    margin-bottom: 214px;
  }
}


.main-wrapper {
  height: 60vh;
  margin: 10px 50px;
  filter: drop-shadow(-1px 5px 3px #ccc);
}
.info-wrapper {
  margin-top: 15px;
}
.map-details {
  text-align: center;
  font-size: 1.2em;
}
.map-details span {
  font-weight: bold;
}
.search-input {
  font-size: 1.2rem;
  width: 80%;
}
.before-sidebar::before {
  content: "*";
  width: 5px;
  height: 100%;
  background-color: #f00530;
}

input:-webkit-autofill,
input:-webkit-autofill:focus {
  transition: background-color 600000s 0s, color 600000s 0s;
}
